import React, { useCallback, useState, useEffect } from 'react'
import axios from 'axios';
import { Table, TableBody, TableCell, TableHead, TableRow } from '@mui/material';
import { Send } from '@mui/icons-material';

const ListEvents = () => {
    const [events, setEvents] = useState([]);
    const getEvents = async () => {
		const { data } = await axios.get(`https://kpirunnersapi.azurewebsites.net/api/UserRegistration?code=ZHVazmf8T8qYw2s5-pKipZQot76giBqaM7NxCRIMBkVKAzFuguAEEg==&entity=events`);
        //const { data } = await axios.get(`http://localhost:3000/users`);
        
        setEvents(data);
    };
    useEffect(() => {
        getEvents();
    }, []);

    return(
    <div>
        
        <div className='center'>
            <h3 style={{alignContent: 'center' }}>Events</h3>
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell style={{fontWeight: 'bold'}}>Event Name</TableCell>
                        <TableCell style={{fontWeight: 'bold'}}>Event Data</TableCell>
                        <TableCell style={{fontWeight: 'bold'}}>Registation Link</TableCell>
                    </TableRow>
                </TableHead>
                {<TableBody>
                    {events.map(event => (<TableRow id={event.eventName}>
                        <TableCell >{event.eventName}</TableCell>
                        <TableCell>{event.eventDate}</TableCell>
                        <TableCell>{event.regLink}</TableCell>
                    </TableRow>)
                    )}
                </TableBody>}
            </Table>
        </div>
    </div>

)}

export default ListEvents